<template>
  <div class="classMedia">
    <div class="mediaTop clearfix">
      <img class="fl" src="./../../assets/picture/media/headLogo/huchenguang.jpg">
      <div class="fl">
        <p class="txt">怀念从前，回不去的从前</p>
      </div>
    </div>
    <div class="mediaBut ">
      <div class="">
        <video-player 
          class="video-player-box classVideo"
          ref="videoPlayer"
          :options="playerOptions"
          :playsinline="true"
          customEventName="customstatechangedeventname"
          
          @play="onPlayerPlay($event)"
          @pause="onPlayerPause($event)"
          @ended="onPlayerEnded($event)"
          @statechanged="playerStateChanged($event)"
          @ready="playerReadied">
        </video-player>
      </div>
    </div>
  </div>
</template>

<script>
require('video.js/dist/video-js.css')
require('vue-video-player/src/custom-theme.css')

export default {
  name: 'classMedia',
  data () {
    return {
      playerOptions: {
        // videojs options
        muted: true,
        language: 'en',
        // playbackRates: [0.7, 1.0, 1.5, 2.0],
        sources: [{
          type: "video/mp4",
          src: require("./../../../static/huchenguang.mp4")
        }],
        poster: require("./../../assets/picture/media/headLogo/huchenguang.jpg"),
      },
      oA: Object,
    }
  },
  computed: {
    player() {
      return this.$refs.videoPlayer.player
    }
  },
  created() {
    this.oA = new Audio();
    this.oA.src = require('./../../../static/GALA.mp3');
    this.oA.play();
  },
  mounted() {
    console.log('this is current player instance object', this.player)
  },
  destroyed() {
    this.oA.pause();
  },
  methods:{
    // listen event
      onPlayerPlay(player) {
        this.oA.pause();
        return false;
        // console.log('player play!', player)
      },
      onPlayerPause(player) {
        // this.oA = new Audio();
        // this.oA.src = './../../../static/GALA.mp3';
        this.oA.play();
        return false;
        // console.log('player pause!', player)
      },
      onPlayerEnded(player){
        this.oA.play();
        return false;
      },

      // or listen state event
      playerStateChanged(playerCurrentState) {
        // console.log('player current update state', playerCurrentState)
      },

      // player is ready
      playerReadied(player) {
        console.log('the player is readied', player)
        // you can use it to do something...
        // player.[methods]
      }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style>
.classMedia{
  width: 100%;
  height: 100%;
}
.mediaTop img{
  margin: 0 0.2rem 0.2rem 0.1rem;
  width: 15%;
}
.mediaTop .txt {
  margin-right: 0.4rem;
  text-align: center;
  line-height: 0.4rem;
  font-size: 0.3rem;
}

/*#vjs_video_3{
  width:100%;
}*/

.classMedia .video-js{
  width: 100%;
}
.video-js .vjs-big-play-button{
  left: 40%;
  top: 40%;
}
</style>



