<template>
  <div class="schoolClass">
    <div>
      <h2><img src="./../assets/LOGO.png" alt="毕业十周年"></h2>
    </div>
    <section class="swiper-container BCindex_container">
      <section class="swiper-wrapper">
        <section class="swiper-slide" v-for="(item,key) in baseData">
          <a><img :src="item.IMG_URL"></a>
        </section>
      </section>
      <section class="swiper-pagination BCindex_page"></section>
    </section> 
    <div class="mediaPack">十年感言</div> 
    <class-media></class-media>
  </div>
</template>

<script>
import Swiper from './../../static/swiper.min.js';
require('./../../static/swiper.min.css');

import ClassMedia from '@/components/class/ClassMedia';

export default {
  name: 'class',
  data () {
    return {
      baseData: [
      {IMG_URL: require('./../assets/picture/dongbaoxing.jpg')},
      {IMG_URL: require('./../assets/picture/pashan.jpg')}
      ],
      swiper: Object
    }
  },
  components: {
    ClassMedia
  },
  mounted() {
    // var oA=new Audio();
    // oA.src='./../../static/GALA.mp3';
    // oA.play();

    this.swiper = new Swiper('.BCindex_container', {
      effect : 'coverflow',
      centeredSlides: true,
      coverflow: {
        rotate: 30,
        stretch: 10,
        depth: 60,
        modifier: 2,
        slideShadows : true
      },
      pagination: '.BCindex_page',
      // loop: true,
      autoplay: 3000,
      observer: true,
      observeParents: true
    });
  },
  methods:{
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style>
.schoolClass{
  width: 100%;
}
.schoolClass h2{
  padding:0.2rem 0.2rem;
  background:url(./../assets/nav_bd.png);
}

.schoolClass h2 img{
  width: 50%;
  margin: 0 auto;
}
.BCindex_container .swiper-list {
  width: 100%;
}

.BCindex_container .swiper-slide img {
  width: 100%;
  height: 4.5rem;
}

.BCindex_container {
  width: 100%;
}

.BCindex_container .swiper-slide {
  text-align: center;
  font-size: 18px;
  background: #fff;
  display: -webkit-box;
  display: -ms-flexbox;
  display: -webkit-flex;
  display: flex;
  -webkit-box-pack: center;
  -ms-flex-pack: center;
  -webkit-justify-content: center;
  justify-content: center;
  -webkit-box-align: center;
  -ms-flex-align: center;
  -webkit-align-items: center;
  align-items: center;
}

.mediaPack{
  width: 95%;
  padding-left: 5%;
  line-height: 0.8rem;
  color: #000;
  font-size: 0.4rem;
}
</style>



